import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

import App from './App.vue'
import router from './router'
import store from './store'

import anime from 'animejs'

const nanoid = require('nanoid').nanoid
const moment = require('moment')

import VueWaypoint from 'vue-waypoint'
import HighchartsVue from 'highcharts-vue';
import Highcharts from 'highcharts';
import vSelect from 'vue-select'

import exportingInit from 'highcharts/modules/exporting';

exportingInit(Highcharts);

import Panel from './components/views/wallet/Panel.vue'

import FontAwesomeIcon from './services/registration/fa-registration'

// Vue.use(LottieAnimation); // add lottie-animation to your global scope
Vue.use(HighchartsVue, { tagName: 'chart' });
Vue.use(VueAxios, axios)
Vue.use(VueWaypoint)

Vue.component('fa', FontAwesomeIcon)
Vue.component('v-select', vSelect)
Vue.component('v-panel', Panel)

Vue.config.productionTip = false

String.prototype.capitalize = function () {
  return this.charAt(0).toUpperCase() + this.slice(1)
}

Vue.prototype.$time = {
  getExpiration: (from, additionalMinutes) => {
    let min = typeof additionalMinutes === 'number' ? additionalMinutes : 0
    return from + 1000 * 60 * min
  },
  toLocale: ts => {
    return new Date(ts)
  },
  format: date => {
    let d = new Date(date)
    let year = d.getFullYear() - 2000
    let month = d.getMonth() + 1
    let day = d.getDate()
    let hours = d.getHours()
    let minutes = d.getMinutes()

    minutes = minutes < 10 ? '0' + minutes : minutes
    hours = hours < 10 ? '0' + hours : hours
    day = day < 10 ? '0' + day : day
    month = month < 10 ? '0' + month : month

    return `${day}.${month}.${year}, ${hours}:${minutes}`
  },
}

Vue.prototype.$axios = {
  post: (api, body) =>
    axios.post(
      window.location.protocol + '//' + window.location.host + api,
      body,
      {
        headers: {
          Authorization: localStorage.getItem('auth-token'),
        },
      },
    ),
  get: api =>
    axios.get(window.location.protocol + '//' + window.location.host + api, {
      headers: {
        Authorization: localStorage.getItem('auth-token'),
      },
    }),
}

Vue.prototype.$anime = anime
Vue.prototype.$nanoid = nanoid
Vue.prototype.$moment = moment

Vue.prototype.$copy = text => {
  let dummy = document.createElement('textarea')
  document.body.appendChild(dummy)
  dummy.value = text
  dummy.select()
  document.execCommand('copy')
  document.body.removeChild(dummy)
}

Vue.prototype.$isDesktop = !navigator.userAgent.match(
  /(iPhone|iPod|iPad|Android|BlackBerry|IEMobile)/,
)

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
