import Vue from 'vue'
import Vuex from 'vuex'

import modules from './modules'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    host: '',
    title: 'AAAHHHHHHHHHH',
  },
  mutations: {
    SET_HOST(state, value) {
      state.host = value
      state.title = value
    },
    SET_TITLE(state, value) {
      state.title = value
    },
  },
  getters: {
    host: state => state.host,
    title: state => state.title
  },
  modules,
})
